nav {
  padding: 0;
  margin: 0;
  display: block;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  display: block;
  color: #1F2933;
  line-height: 1.4;
  margin: 0;
  padding: 0;
  margin-bottom: 6px;
}

p {
  color: #323F4B;
  line-height: 1.7;
  padding: 0;
  margin: 0;
