}
.grid.grid-33x3-layout, .grid.grid-50x2-layout {
  grid-template-columns: 1fr;
  row-gap: 24px;
  column-gap: 24px;
}

@media (min-width: 972px) {
  .grid.grid-50x2-layout {
    grid-template-columns: 1fr 1fr;
  }
  .grid.grid-33x3-layout {
