.menu-toggle {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
  -ms-flex-pack: center;
  justify-content: center;
  -webkit-box-align: center;
  -ms-flex-align: center;
  align-items: center;
  background-color: transparent;
  color: transparent;
}
.menu-toggle .menu-icon {
  display: block;
  margin: 0 auto;
  position: relative;
}
.menu-toggle .menu-icon .menu-bar {
  display: block;
  height: 3px;
  width: 30px;
  border-radius: 6px;
  margin-bottom: 6px;
  background-color: currentColor;
  -webkit-transition: all 0.2s ease-in-out;
  -o-transition: all 0.2s ease-in-out;
  transition: all 0.2s ease-in-out;
}
.menu-toggle .menu-icon .menu-bar:nth-child(1) {
  margin-top: 2px;
}
.menu-toggle .menu-label {
  display: inline-block;
  position: relative;
  margin-top: 6px;
  margin-right: 8px;
  color: currentColor;
  text-transform: uppercase;
  letter-spacing: 2px;
  font-weight: bold;
  font-size: 11pt;
  cursor: pointer;
}
.menu-toggle.open .menu-bar:nth-child(1) {
  opacity: 0;
}
.menu-toggle.open .menu-bar:nth-child(2) {
  -webkit-transform: rotate(-45deg);
  -ms-transform: rotate(-45deg);
  transform: rotate(-45deg);
  -webkit-transform-origin: center 0.5px;
  -ms-transform-origin: center 0.5px;
  transform-origin: center 0.5px;
  background-color: currentColor;
}
.menu-toggle.open .menu-bar:nth-child(3) {
  -webkit-transform: rotate(45deg);
  -ms-transform: rotate(45deg);
  transform: rotate(45deg);
  -webkit-transform-origin: 24.85px -2.5px;
  -ms-transform-origin: 24.85px -2.5px;
  transform-origin: 24.85px -2.5px;
  background-color: currentColor;
}
.menu-toggle.open .menu-label {
  color: currentColor;
}

.nav {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  display: -ms-grid;
  display: grid;
  grid-template-columns: 65% 35%;
  grid-template-areas: "brandLink menuBtn";
  column-gap: 0;
  row-gap: 0;
  z-index: 15;
}
.nav .nav-item {
  display: flex;
  align-items: center;
}
.nav .nav-item.brand-link {
  grid-area: brandLink;
  color: currentColor;
  font-weight: 600;
  font-size: 14pt;
  letter-spacing: 2px;
  padding: 0;
  padding-left: 16px;
  box-shadow: none;
}
.nav .nav-item.brand-link .brand-link-icon path {
  fill: currentColor;
}
.nav .navigation {
  display: block;
  position: absolute;
  top: 48px;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
  clip: rect(0, 0, 0, 0);
  padding: 32px 0;
  background-color: inherit;
  z-index: 14;
  color: currentColor;
}
.nav .navigation .menu-item {
  display: block;
  text-align: center;
  color: currentColor;
}
.nav .navigation .menu-item .menu-link {
  font-size: 16pt;
  box-shadow: none;
  color: currentColor;
}
.nav .navigation.expanded {
  width: 100%;
  min-height: 25vh;
  clip: auto;
}
.nav .menu-btns {
  justify-content: flex-end;
  grid-area: menuBtn;
}
.nav .menu-btns .menu-button {
  margin: 0;
}
.nav .menu-btns .menu-button #menuBtn {
  margin-right: 16px;
}

@media screen and (min-width: 972px) {
  .nav {
    display: grid;
    grid-template-areas: "brandLink navLinks";
    grid-template-columns: 200px 80%;
  }
  .nav .nav-item.brand-link {
    padding-bottom: 0;
    grid-area: brandLink;
  }
  .nav .nav-item.navigation {
    position: static;
    display: flex;
    width: 100%;
    grid-area: navLinks;
    box-shadow: none;
  }
  .nav .nav-item.navigation .menu-item {
    display: inline-block;
    width: auto;
    margin: 0;
    margin-right: 24px;
  }
  .nav .nav-item.navigation .menu-item .menu-link {
    font-size: 11pt;
  }
  .nav .nav-item.navigation .menu-item .menu-link:hover, .nav .nav-item.navigation .menu-item .menu-link:focus {
    box-shadow: inset 0 -2px 0 0 currentColor;
  }
  .nav .nav-item.navigation .menu-item:last-child {
    margin-right: 0;
  }
  .nav .nav-item.menu-btns {
    display: none;
  }
}
